export interface CardInfo {
  id: Number,
  name: String,
  cardNumber: String,
  expires: String,
  cvv: String,
  ammount: Number,
  policy: Boolean,
}

export class Card implements CardInfo {
  id = 0;
  name = '';
  cardNumber = '';
  expires = '';
  cvv = '';
  ammount = 0;
  policy = false;
}